<template>
    <div>
        <van-nav-bar
            title="个人信息"
            left-text="返回"
            left-arrow
            @click-left="clickReturn"
        />
        <div class="user-img">
            <div class="user-img-title fl">头像</div>
            <div class="user-img-imgbox fr">
                <img :src="userInfo.userImg" class="auto-img">
                <van-uploader :after-read="afterRead" />
            </div>     
        </div>
        <van-cell-group>
            <van-cell title="昵称" @click="showNickName" :value="userInfo.nickName" />
            <van-cell title="电话" :value="userInfo.phone" />
            <van-popup v-model="nickNameShow">
                <input class="nickName" ref="nickName" type="text" @change="nc">
            </van-popup>
            <van-cell title="简介" @click="showUserDescShow" :value="userInfo.desc" />
            <van-popup v-model="userDescShow">
                <input class="userDesc" ref="userDesc" type="text" @change="jj">
            </van-popup>
        </van-cell-group>
    </div>
</template>

<script>

import "../assets/css/account.less";


    export default {
        data(){
            return {
                userInfo: {},
                userDescShow: false,
                nickNameShow: false,
                desc:'',
                nickName:''
            }
        },

        created() {
            this.getUserInfo();
        },

        methods:{
            getUserInfo() {
                let tokenString = localStorage.getItem('__Tk');

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: 'GET',
                    url: '/findAccountInfo',
                    params: {
                    appkey: this.appkey,
                    tokenString
                    }
                }).then(result => {
                    this.$toast.clear();
                    console.log('result ==> ', result);

                    if (result.data.code == 'B001') {
                    this.userInfo = result.data.result[0];
                    }

                }).catch(err => {
                    this.$toast.clear();
                    console.log('err ==> ', err);
                })
            },

            clickReturn(){
                this.$router.go(-1);
            },

            showUserDescShow() {
                this.userDescShow = true;
            },

            showNickName() {
                this.nickNameShow = true;
            },

            jj(){
                this.desc=this.$refs.userDesc.value;
                this.axios({
                    method: 'POST',
                    url: '/updateDesc',
                    data: {
                        appkey: this.appkey,
                        tokenString: localStorage.getItem("__Tk"),
                        desc:this.desc
                    }
                }).then(result => {
                    //关闭加载提示
                    this.$toast.clear();
                    console.log('result ==> ', result);
                    this.$toast({
                        message: result.data.msg
                    });
                    this.getUserInfo();

                }).catch(err => {
                    //关闭加载提示
                    this.$toast.clear();
                    console.log('err ==> ', err);
                })
            },

            nc(){
                this.nickName=this.$refs.nickName.value;
                this.axios({
                    method: 'POST',
                    url: '/updateNickName',
                    data: {
                        appkey: this.appkey,
                        tokenString: localStorage.getItem("__Tk"),
                        nickName:this.nickName
                    }
                }).then(result => {
                    //关闭加载提示
                    this.$toast.clear();
                    console.log('result ==> ', result);
                    this.$toast({
                        message: result.data.msg
                    });
                    this.getUserInfo();
                }).catch(err => {
                    //关闭加载提示
                    this.$toast.clear();
                    console.log('err ==> ', err);
                })
            },

            afterRead(file) {

                let img=file.content.split(',');
                this.axios({
                    method: 'POST',
                    url: '/updateAvatar',
                    data: {
                        appkey: this.appkey,
                        tokenString: localStorage.getItem("__Tk"),
                        imgType: 'jpg',
                        serverBase64Img:img[1]
                    }
                }).then(result => {

                    this.$toast.clear();

                    this.$toast({
                        message: result.data.msg
                    });
                    this.getUserInfo();
                }).catch(err => {

                    this.$toast.clear();

                })
            }
        }
    }
</script>
